import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { ConnectedRouter } from 'react-router-redux'; // 
import createBrowserHistory from 'history/createBrowserHistory'; // 
import './index.css';
import App from './App';
// GeoLocation API
import createStore from './createStore'; // 
import registerServiceWorker from './registerServiceWorker';

// history
const history = createBrowserHistory();

const store = createStore(history);
ReactDOM.render(
	<Provider store={store} >
		<ConnectedRouter history={history}>
			<App />
		</ConnectedRouter>
	</Provider>, document.getElementById('root'));
registerServiceWorker();

// 
if (navigator.geolocation) {
	// 
	navigator.geolocation.getCurrentPosition(

		// [1] 
		function (position) {
			// 
			var data = position.coords;

			// 
			var lat = data.latitude;
			var lng = data.longitude;
			var alt = data.altitude;
			var accLatlng = data.accuracy;
			var accAlt = data.altitudeAccuracy;
			var heading = data.heading;			//0=,90=,180=,270=
			var speed = data.speed;

			// 
			//			alert( "\n[" + lat + "," + lng + "]\n" ) ;

			// HTML
			document.getElementById('result').innerHTML = '<dl><dt></dt><dd>' + lat + '</dd><dt></dt><dd>' + lng + '</dd><dt></dt><dd>' + alt + '</dd><dt></dt><dd>' + accLatlng + '</dd><dt></dt><dd>' + accAlt + '</dd><dt></dt><dd>' + heading + '</dd><dt></dt><dd>' + speed + '</dd></dl>';


			setInterval(function () {
				var locateData = position.coords;

				// 
				var lat = locateData.latitude;
				var lng = locateData.longitude;
				var alt = locateData.altitude;
				var data = { "lat": lat, "lng": lng, "alt": alt };
				var gpsdata = JSON.stringify(data);

				// $.ajax({
				// 	type : 'post',
				// 	url : "https://192.168.11.9:8443/gps/data",
				// 	data : gpsdata,
				// 	contentType: 'application/JSON',
				// 	dataType : 'JSON',
				// 	scriptCharset: 'utf-8',
				// 	success : function(data) {
				// 	}
				// });
			}, 60000);
			// 

			// Google Maps

			// 

		},

		// [2] 
		function (error) {
			// (error.code)
			// 0:UNKNOWN_ERROR				
			// 1:PERMISSION_DENIED			
			// 2:POSITION_UNAVAILABLE		
			// 3:TIMEOUT					

			// 
			var errorInfo = [
				"",
				"",
				"",
				""
			];

			// 
			var errorNo = error.code;

			// 
			var errorMessage = "[: " + errorNo + "]\n" + errorInfo[errorNo];

			// 
			alert(errorMessage);

			// HTML
			document.getElementById("result").innerHTML = errorMessage;
		},

		// [3] 
		{
			"enableHighAccuracy": false,
			"timeout": 8000,
			"maximumAge": 2000,
		}

	);
}

// 
else {
	// 
	var errorMessage = "GeoLacation API";

	// 
	alert(errorMessage);

	// HTML
	document.getElementById('result').innerHTML = errorMessage;
}



